import React, {useState} from 'react'
import {homeWorkReducer} from './bll/homeWorkReducer'
import SuperButton from '../h4/common/c2-SuperButton/SuperButton'
import styles from "./HW8.module.css";

export type UserType = {
    _id: number
    name: string
    age: number
}

const initialPeople: Array<UserType>  = [
    {_id: 0, name: '', age: 3},
    {_id: 1, name: '', age: 66},
    {_id: 2, name: '', age: 16},
    {_id: 3, name: '', age: 44},
    {_id: 4, name: '', age: 40},
    {_id: 5, name: '', age: 55},
]

function HW8() {
    const [people, setPeople] = useState<Array<UserType>>(initialPeople) // need to fix any

    // need to fix any
    const finalPeople = people.map((p: UserType) => (
        <div key={p._id}>
            <table>
                <tr className={styles.table}>
                    <td className={styles.text}>{p.name}</td>
                    <td className={styles.text}>{p.age}</td>
                </tr>
            </table>
        </div>
    ))

    const sortUp = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'up'}))
    const sortDown = () => setPeople(homeWorkReducer(initialPeople, {type: 'sort', payload: 'down'}))
    const check = () => setPeople(homeWorkReducer(initialPeople, {type: 'check', payload: 18}))

    return (
        <div>
            <hr/>
            homeworks 8
            {/*should work ( )*/}
            {finalPeople}
            <table>
                <tr className={styles.table}>
                    <td><SuperButton onClick={sortUp}>sort up</SuperButton></td>
                    <td><SuperButton onClick={sortDown}>sort down</SuperButton></td>
                    <td><SuperButton onClick={check}>check 18</SuperButton></td>
                    <hr/>
                    {/*  ,  */}
                    {/*<AlternativePeople/>*/}
                </tr>
            </table>
            <hr/>
        </div>
    )
}

export default HW8
